.App {
  text-align: center;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

ul,
li {
  list-style-type: none;
  padding: 0;
  margin: 0;
}


.start-game {
  display: flex;
  flex-direction: column;
  row-gap: 25px;
}

.game-icon {}

.start-btn,
.btn {
  background: #0468FF;
  border: 1px solid #0468FF;
  border-radius: 6px;
  padding: 12px 22px;
  align-items: stretch;
  color: #fff;
  font-size: 24px;
  cursor: pointer;
  transition: 0.5s;
  min-width: 150px
}

.btn:hover {
  opacity: 0.8;

}

.shuffle-list {
  display: flex;
  column-gap: 15px;
  margin-bottom: 30px;
}

.shuffle-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  row-gap: 25px;

}

.shuffle-letter {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 100px;
  background-color: rgb(169, 215, 255);
  box-shadow: 0 2px 4px rgb(0 0 0 / 25%);
  border-radius: 10px;
  font-size: 36px;
  font-weight: bold;
}

.shuffle-item input {
  width: 100px;
  height: 100px;
  font-size: 36px;
  font-weight: bold;
  box-shadow: 0 2px 4px rgb(0 0 0 / 25%);
  border-radius: 10px;
  border: none;
  outline: none;
  text-align: center;
  background-color: #ffbebe;

}

.shuffle-item input.correct {
  background-color: #68e794;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.timer {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 25px;
}

.results-table {
  /* border: 2px solid #000; */
  width: 300px;
  font-size: 28px;
  font-weight: bold;
}

.results-item {
  display: flex;
  /* column-gap: 50px; */
  justify-content: space-between;
  padding: 10px 25px;
  font-size: 24px;
  font-weight: bold;
  /* border-bottom: 2px solid #000; */
  background-color: #a9d7ff;
  border-radius: 10px;
}

.results-item:not(:last-child) {
  margin-bottom: 15px;
}

.results-item:nth-child(even) {
  background-color: #ffbebe;
}

.results-item:nth-child(add) {}

.results-item {}

.item-value {}

.results-controls {
  display: flex;
  flex-direction: column;
  row-gap: 25px;

}

.clear-btn {
  margin-top: 30px;
}